body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #a3dbf2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
    margin: 0;
}

.card {
    background: white;
    border-radius: 10px;
}

.card-img {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 220px;
}

.image {
    max-width: 200px;
    max-height: 200px;
}

.card-types {
    display: flex;
    justify-content: center;    
}

.card-type {
    padding: 5px 10px;
    margin: 10px 5px;
    border-radius: 5px;
    background: cyan;
    color: #fff;
    width: 100px;
    text-align: center;
}

.card-name {
    text-align: center;
    text-transform: capitalize;
    font-weight: 800;
    font-size: 22px;
}

.card-property-info {
    margin: 0px 0px 10px 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.property-info {
    display: flex;
    align-items: center;
    height: 30px;
    grid-gap: 20px;
    gap: 20px;
}

.property-title {
    font-weight: 700;
    font-size: 15px;
    width: 70px;
}

.property-data {
    font-weight: 500;
}

.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
    background: #53c3fa;
    color: white;
    
}

.title {
    color: #254246;

    @media only screen and (max-width: 470px) {
        text-align: center;
        font-size: 22px;
    }
}

.logo {
    @media only screen and (max-width: 800px) {
        width: 100px;
    }
}

* {
  box-sizing: border-box;
}

.button-container {
  display: flex;
  justify-content: center;
  grid-gap: 30px;
  gap: 30px;
  margin: 40px 0px;

  @media only screen and (max-width: 500px) {
    flex-direction: column;
    align-items: center;
  }
}

.grid-container {
  width: 70%;
  display: grid;
  margin: 0 auto;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 15px;

  @media only screen and (max-width: 450px) {
    width: 90%;
  }
}

.searchbar {
  margin: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;

}

.search {
  width: 200px;
  height: 40px;
  border-radius: 10px;
  border: 1px solid #a3dbf2;
  text-align: center;
  font-size: 16px;
}

.search-button {
  width: 100px;
  height: 30px;
  border-radius: 10px;
  border: none;
  background-color: #006cbf;
  color: white;
  text-align: center;
  font-size: 16px;
  cursor: pointer;
}

.card-container {
  width: 70%;
  display: grid;
  margin: 0 auto;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 10px;
  max-width: 400px;
}

.button {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;
  margin: 20px 0;

  @media only screen and (max-width: 450px) {
    flex-direction: column;
    align-items: center;
  }
}

.button button {
  border-style: none;
  cursor: pointer;
  padding: 5px 10px;
  /* margin-right: 10px; */
  border-radius: 5px;
  background: #30a7d7;
  color: #fff;
  font-size: 1em;
  width: 150px;
}

